import React, { useState, useEffect, useCallback } from "react";
import Layout from "../components/Layout";
import { useParams } from "react-router-dom";
import axios from "axios";
import { DatePicker, message, TimePicker } from "antd";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { showLoading, hideLoading } from "../redux/features/alertSlice";

const BookingPage = () => {
  const { user } = useSelector((state) => state.user);
  const params = useParams();
  const [teacher, setTeacher] = useState({});
  const [date, setDate] = useState(null);
  const [time, setTime] = useState(null);
  const dispatch = useDispatch();

  const getUserData = useCallback(async () => {
    try {
      const res = await axios.post(
        "/api/v1/teacher/getTeacherById",
        { teacherId: params.teacherId },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        setTeacher(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  }, [params.teacherId]);

  const handleAvailability = async () => {
    if (!date || !time) {
      message.error("Date and Time are required");
      return;
    }

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/user/booking-availability",
        {
          teacherId: params.teacherId,
          date,
          time,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      dispatch(hideLoading());
      if (res.data.success) {
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading());
      console.log(error);
    }
  };

  const handleBooking = async () => {
    if (!date || !time) {
      message.error("Date and Time are required");
      return;
    }

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/user/book-appointment",
        {
          teacherId: params.teacherId,
          userId: user._id,
          teacherInfo: teacher,
          userInfo: user,
          date,
          time,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      dispatch(hideLoading());
      if (res.data.success) {
        message.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading());
      console.log(error);
    }
  };

  useEffect(() => {
    getUserData();
  }, [getUserData]);

  return (
    <Layout>
      <h1 className="text-center">Booking Page</h1>
      <div className="container m-2">
        {teacher && (
          <div>
            <h4>
              Prof. {teacher.firstName} {teacher.lastName}
            </h4>
            <h5>Fees: {teacher.feesPerConsultation}</h5>
            <h5>
              Timings: {teacher.timings && teacher.timings[0]} -{" "}
              {teacher.timings && teacher.timings[1]}
            </h5>
            <div className="d-flex flex-column w-50">
              <DatePicker
                className="m-2"
                format="DD-MM-YYYY"
                onChange={(value) => {
                  setDate(value ? value.format("DD-MM-YYYY") : null);
                }}
                value={date ? moment(date, "DD-MM-YYYY") : null}
              />
              <TimePicker
                format="HH:mm"
                className="m-2"
                onChange={(value) => {
                  setTime(value ? value.format("HH:mm") : null);
                }}
                value={time ? moment(time, "HH:mm") : null}
              />
              <div>
                <p>Selected Date: {date}</p>
                <p>Selected Time: {time}</p>
              </div>
              <button
                className="btn btn-primary m-2"
                style={{
                  boxShadow:
                    "3px 3px 10px rgba(0,0,0,0.5), -2px -2px 8px rgba(255, 255, 255, 0.4)",
                }}
                onClick={handleAvailability}
              >
                Check Availability
              </button>
              <button
                className="btn m-2"
                style={{
                  backgroundColor: "#007367",
                  color: "white",
                  boxShadow:
                    "3px 3px 10px rgba(0,0,0,0.5), -2px -2px 8px rgba(255, 255, 255, 0.4)",
                }}
                onClick={handleBooking}
              >
                Book Now
              </button>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default BookingPage;
